@import "src/styles/vars.sass";

.section2 {
    margin: 0;
    padding-top: 140px;
    @include tablet {
        padding-top: 120px; }
    @include mobile {
        padding-top: 70px; }

    .content-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include mobile {
            text-align: center; }

        .title {
            font-family: $overpas;
            font-weight: 600;
            font-size: 42px;
            line-height: 51px;
            margin-bottom: 40px; }

        .grid-wrapper {
            display: grid;
            grid-template-columns: 2fr 4fr;
            grid-column-gap: 1em;
            @include mobile {
                grid-template-columns: 1fr; }

            .box-1 {

                .img-central {
                    margin-bottom: 30px;

                    .mob-size {
                        @include tablet {
                            max-width: 216px;
                            height: auto; } } } }

            .box-2 {
                .section2-h3 {
                    font-family: $overpas;
                    font-weight: 600;
                    font-size: 26px;
                    line-height: 31px;
                    margin-bottom: 15px; }

                .section2-p1 {
                    font-family: $sourcesans;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 22px; }

                .sing-up-link {
                    color: $primary;
                    border: none;
                    background: transparent;
                    font-family: $sourcesans;
                    font-size: 22px;
                    line-height: 60px;
                    font-weight: 600;

                    .in-link {
                        color: $primary;
                        &:hover {
                            text-decoration: none;
                            color: #fc831f; } } } } } } }
